<template>
  <div class="menu">
    <div v-for="snippet of snippets" v-bind:key="snippet.id" class="menu-item">
      <menu-item :snippet="snippet"></menu-item>
    </div>
  </div>
</template>

<script>
import MenuItem from "./menu/MenuItem.vue";

export default {
  name: "Menu",
  components: {
    MenuItem
  },
  computed: {
    snippets() {
      return this.$store.getters.snippets;
    }
  }
};
</script>

<style scoped>
.menu {
  overflow-y: auto;
  display: grid;
  grid-template-rows: repeat(auto-fill, 20px);
  grid-gap: 5px
}

.menu-item {
    align-self: center
}
</style>