<template>
  <div class="main-content">
    <div class="title">Code</div>
    <div class="code">
      <pre>{{snippet.code}}</pre>
    </div>

    <div class="title">Description</div>
    <div v-html="snippet.description"></div>

    <div class="title">References</div>
    <div class="references">
      <div v-for="(reference, index) of snippet.references" v-bind:key="reference">
        <a :href="reference" target="_blank" :title="reference">Reference {{index + 1}}</a>
      </div>
    </div>

    <div class="title">Contributor</div>
    <div><a :href="snippet.contributor.link" :title="snippet.contributor.link">{{snippet.contributor.name}}</a></div>
  </div>
</template>

<script>
export default {
  name: "mainContent",
  computed: {
    snippet() {
      return this.$store.getters.activeSnippet;
    }
  }
};
</script>
